import React, { useState, useEffect, useRef } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import history from "./history";
import {
  Container,
  Button,
  Popup,
  Image,
  Input,
  Icon,
  TextArea,
  Form,
  Segment,
  Dropdown,
  Step,
  Header,
  Card,
  Rating,
  Ref,
  Dimmer,
  Loader,
  Modal,
  Statistic,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import "react-datepicker/dist/react-datepicker.css";
import userData from "./employee.json";

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  return new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    hours,
    minutes,
    seconds
  );
}

function Fechas(fecha) {
  var dd = fecha.getDate();
  var mm = fecha.getMonth() + 1;
  var yyyy = fecha.getFullYear();
  var hora = fecha.getHours();
  var minutos = fecha.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = fecha.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = fecha.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let resultado = yyyy + "-" + mm + "-" + dd;
  return resultado;
}

function Home({ user }) {
  const [data, setData] = useState();
  const [hoy, setHoy] = useState();
  const [ayer, setAyer] = useState();
  const [mes, setMes] = useState();
  const [mesAnterior, setMesAnterior] = useState();

  //Fechas
  let today = new Date();
  let yesterday = new Date(today);
  yesterday.setDate(yesterday.getDate() - 1);
  var start = new Date("1900-01-01");
  var gap = ExcelDateToJSDate(
    Math.round((today - start) / (1000 * 60 * 60 * 24)) + 1
  )
    .toISOString()
    .slice(0, 10);
  //Fechas
  const sacardata = async () => {
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:S" + lastr.toString());
    for (j = 1; j < lastr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      const cr = sheet.getCell(j, 2);
      const dr = sheet.getCell(j, 3);
      const er = sheet.getCell(j, 4);
      const fr = sheet.getCell(j, 5);
      const gr = sheet.getCell(j, 6);
      const hr = sheet.getCell(j, 7);
      const ir = sheet.getCell(j, 8);
      const jr = sheet.getCell(j, 9);
      const kr = sheet.getCell(j, 10);
      const lr = sheet.getCell(j, 11);
      const mr = sheet.getCell(j, 12);
      const nr = sheet.getCell(j, 13);
      const or = sheet.getCell(j, 14);
      const pr = sheet.getCell(j, 15);
      const qr = sheet.getCell(j, 16);
      const rr = sheet.getCell(j, 17);
      const sr = sheet.getCell(j, 18);
      datas.push({
        id: sr.value,
        fecha: ar.value,
        fecha_hora: br.value,
        nombre: cr.value,
        area: dr.value,
        rubro: er.value,
        categoria: fr.value,
        observaciones: gr.value,
        severidad: hr.value,
        pac: ir.value,
        fecha_cierre: jr.value,
        responsable: kr.value,
        anexo: lr.value,
        status: mr.value,
        confirmado: nr.value,
        extension: or.value,
        nueva_fecha: pr.value,
        detalle: qr.value,
        anexo2: rr.value,
      });
    }
    setData(datas);
    console.log(datas);
  };

  useEffect(() => {
    if (!data) {
      sacardata();
    }
    if (data) {
      setHoy(
        data.filter((item) => {
          return (
            ExcelDateToJSDate(item.fecha).toISOString().slice(0, 10) ===
            today.toISOString().slice(0, 10)
          );
        })
      );
      setAyer(
        data.filter((item) => {
          return (
            ExcelDateToJSDate(item.fecha).toISOString().slice(0, 10) ===
            yesterday.toISOString().slice(0, 10)
          );
        })
      );
      setMes(
        data.filter((item) => {
          return ExcelDateToJSDate(item.fecha).getMonth() === today.getMonth();
        })
      );
      setMesAnterior(
        data.filter((item) => {
          return (
            ExcelDateToJSDate(item.fecha).getMonth() === today.getMonth() - 1
          );
        })
      );
    }
  }, [data]);

  return (
    <Container>
      <Segment>
        <Header>{today.toISOString().slice(0, 10)}</Header>
        {hoy && ayer && mes && mesAnterior && <Header>Registros</Header>}
        {hoy && ayer && mes && mesAnterior && (
          <Statistic.Group>
            <Statistic>
              <Statistic.Value>{hoy.length}</Statistic.Value>
              <Statistic.Label>HOY</Statistic.Label>
            </Statistic>
            <Statistic>
              <Statistic.Value>{ayer.length}</Statistic.Value>
              <Statistic.Label>AYER</Statistic.Label>
            </Statistic>
            <Statistic>
              <Statistic.Value>{mes.length}</Statistic.Value>
              <Statistic.Label>Mes Actual</Statistic.Label>
            </Statistic>
            <Statistic>
              <Statistic.Value>{mesAnterior.length}</Statistic.Value>
              <Statistic.Label>Mes Anterior</Statistic.Label>
            </Statistic>
          </Statistic.Group>
        )}
      </Segment>
    </Container>
  );
}

export default Home;
