import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import { storage } from "./firebase/firebase";
import imageCompression from "browser-image-compression";
import history from "./history";
import {
  Container,
  Button,
  Popup,
  Image,
  Input,
  Icon,
  TextArea,
  Form,
  Segment,
  Dropdown,
  Statistic,
  Divider,
  Dimmer,
  Loader,
  Pagination,
  Card,
  Label,
  Checkbox,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function xltoDate(serial) {
  var utc_days = Math.floor(serial - 25569);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var fecha_js = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate()
  );

  var mm = fecha_js.getMonth() + 1; // getMonth() is zero-based
  var dd = fecha_js.getDate();

  var fecha_txt = [
    fecha_js.getFullYear(),
    (mm > 9 ? "" : "0") + mm,
    (dd > 9 ? "" : "0") + dd,
  ].join("-");

  return fecha_txt;
}

function Tareas({ user }) {
  //Fechas
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  //Fechas
  const [data, setData] = useState("");
  const [myData, setMyData] = useState("");
  const [myPac, setMyPac] = useState("");
  const [activoID, setActivoID] = useState("");
  const [pagina, setPagina] = useState(1);
  const [totalPagina, setTotalPagina] = useState(0);
  const [extension, setExtension] = useState("N");
  const [extDate, setExtDate] = useState(date);
  const [detalle, setDetalle] = useState("");

  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const [preliminar, setPreliminar] = useState(false);

  async function handleImageUpload(event) {
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      console.log("originalFile instanceof Blob", imageFile instanceof Blob); // true
      console.log(`originalFile size ${imageFile.size / 1024 / 1024} MB`);
      var reader = new FileReader();
      var preview = document.querySelector("#blah");
      console.log(preview);
      reader.onload = function (e) {
        setPreliminar(true);
        preview.setAttribute("src", e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        console.log(
          "compressedFile instanceof Blob",
          compressedFile instanceof Blob
        ); // true
        console.log(
          `compressedFile size ${compressedFile.size / 1024 / 1024} MB`
        ); // smaller than maxSizeMB
        setImageAsFile((imageFile) => compressedFile);
      } catch (error) {
        console.log(error);
      }
      setSubir(true);
    } else {
      setSubir(false);
      setImageAsFile("");
      setImageAsUrl("");
      setPreliminar(false);
      var preview = document.querySelector("#blah");
      preview.setAttribute("src", "");
      var archivoSelect = document.querySelector("#anexoImg");
      console.log(archivoSelect);
      archivoSelect.value = "";
    }
  }

  const handleFireBaseUpload = (e) => {
    e.preventDefault();
    console.log("start of upload");
    console.log(imageAsFile);
    if (imageAsFile === "") {
      console.error(`not an image, the image file is a ${typeof imageAsFile}`);
    }
    const uploadTask = storage
      .ref(`/images/${imageAsFile.name}`)
      .put(imageAsFile);
    uploadTask.on(
      "state_changed",
      (snapShot) => {
        //takes a snap shot of the process as it is happening
        console.log(snapShot);
      },
      (err) => {
        //catches the errors
        console.log(err);
      },
      () => {
        // gets the functions from storage refences the image storage in firebase by the children
        // gets the download url then sets the image from firebase as the value for the imgUrl key:
        storage
          .ref("images")
          .child(imageAsFile.name)
          .getDownloadURL()
          .then((fireBaseUrl) => {
            setImageAsUrl((prevObject) => ({
              ...prevObject,
              imgUrl: fireBaseUrl,
            }));
          });
      }
    );
  };

  const sacardata = async () => {
    var datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:S" + lastr.toString());
    for (j = 1; j < lastr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      const cr = sheet.getCell(j, 2);
      const dr = sheet.getCell(j, 3);
      const er = sheet.getCell(j, 4);
      const fr = sheet.getCell(j, 5);
      const gr = sheet.getCell(j, 6);
      const hr = sheet.getCell(j, 7);
      const ir = sheet.getCell(j, 8);
      const jr = sheet.getCell(j, 9);
      const kr = sheet.getCell(j, 10);
      const lr = sheet.getCell(j, 11);
      const mr = sheet.getCell(j, 12);
      const nr = sheet.getCell(j, 13);
      const or = sheet.getCell(j, 14);
      const pr = sheet.getCell(j, 15);
      const qr = sheet.getCell(j, 16);
      const rr = sheet.getCell(j, 17);
      const sr = sheet.getCell(j, 18);
      datas.push({
        id: sr.value,
        fecha: ar.value,
        fecha_hora: br.value,
        nombre: cr.value,
        area: dr.value,
        rubro: er.value,
        categoria: fr.value,
        observaciones: gr.value,
        severidad: hr.value,
        pac: ir.value,
        fecha_cierre: jr.value,
        responsable: kr.value,
        anexo: lr.value,
        status: mr.value,
        confirmado: nr.value,
        extension: or.value,
        nueva_fecha: pr.value,
        detalle: qr.value,
        anexo2: rr.value,
      });
    }
    setData(datas);
    setMyData(
      datas.filter((item) => {
        return item.nombre === user;
      })
    );
    console.log(datas.filter((item) => {
      return item.responsable === user;
    }))
    setMyPac(
      datas.filter((item) => {
        return item.responsable === user && item.status !== "Completado";
      })
    );
    
    setTotalPagina(
      datas.filter((item) => {
        return item.responsable === user && item.status !== "Completado";
      }).length
    );
    try {
      setActivoID(
        datas.filter((item) => {
          return item.responsable === user && item.status !== "Completado";
        })[0].id
      );
      setExtension(
        datas.filter((item) => {
          return item.responsable === user && item.status !== "Completado";
        })[0].extension
      );
      console.log(
        datas.filter((item) => {
          return item.responsable === user && item.status !== "Completado";
        })[0].id
      );
    } catch (err) {
      console.log(err);
    }
  };

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      console.log(ele);
      if (ele === "Usuario") {
        priceEls[i].click();
        break;
      }
    }
  };

  const registrar = async () => {
    let extendido = extension;
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:S" + lastr.toString());
    for (j = 1; j < lastr; j++) {
      if (sheet.getCell(j, 18).value === activoID) {
        const mr = sheet.getCell(j, 12);
        const or = sheet.getCell(j, 14);
        const pr = sheet.getCell(j, 15);
        const qr = sheet.getCell(j, 16);
        const rr = sheet.getCell(j, 17);
        mr.value = "Pendiente Aprobacion";
        or.value = extendido;
        if (extDate) {
          pr.formula =
            "=date(" +
            extDate.substring(0, 4) +
            "," +
            extDate.substring(5, 7) +
            "," +
            extDate.substring(8, 10) +
            ")";
        }
        if (detalle) {
          qr.value = detalle;
        }
        if (imageAsUrl.imgUrl) {
          rr.value = imageAsUrl.imgUrl;
        }
        await sheet.saveUpdatedCells();
        console.log(pr.value);
        break;
      }
    }

    console.log("saved");
  };

  useEffect(() => {
    if (!data) {
      sacardata();
    }
  }, [data]);

  return (
    <Container>
      {user ? (
        <>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Pagination
              defaultActivePage={1}
              ellipsisItem={{
                content: <Icon name="ellipsis horizontal" />,
                icon: true,
              }}
              firstItem={{
                content: <Icon name="angle double left" />,
                icon: true,
              }}
              lastItem={{
                content: <Icon name="angle double right" />,
                icon: true,
              }}
              prevItem={{ content: <Icon name="angle left" />, icon: true }}
              nextItem={{ content: <Icon name="angle right" />, icon: true }}
              totalPages={totalPagina}
              onPageChange={(e, { activePage }) => {
                setPagina(activePage);
                console.log(myPac[activePage - 1]);
                setActivoID(myPac[activePage - 1].id);
                setSubir(false);
                setImageAsUrl("");
                setImageAsFile("");
                setPreliminar(false);
                setExtension(myPac[activePage - 1].extension);
                setExtension(date);
                var preview = document.querySelector("#blah");
                preview.setAttribute("src", "");
                var archivoSelect = document.querySelector("#anexoImg");
                archivoSelect.value = "";
              }}
            />
          </div>

          <Segment>
            <Dimmer active={!data}>
              <Loader>Loading</Loader>
            </Dimmer>
            <Statistic.Group>
              <Statistic>
                <Statistic.Value>{myData.length}</Statistic.Value>
                <Statistic.Label>Observaciones Registradas</Statistic.Label>
              </Statistic>
              <Divider />
              <Statistic>
                <Statistic.Value>{myPac.length}</Statistic.Value>
                <Statistic.Label>Plan de Accion Pendientes</Statistic.Label>
              </Statistic>
              <Divider />
              <Statistic>
                <Statistic.Value>22</Statistic.Value>
                <Statistic.Label>Tasks</Statistic.Label>
              </Statistic>
            </Statistic.Group>
          </Segment>
          <Segment
            style={{
              backgroundColor: "whitesmoke",
              height: "60vh",
              overflowY: "scroll",
            }}
          >
            {myPac && myPac.length !==0 ? (
              <>
                <Card
                  fluid
                  header={myPac[pagina - 1].rubro.toUpperCase()}
                  meta={
                    myPac[pagina - 1].categoria +
                    " - " +
                    myPac[pagina - 1].area +
                    " - Autor: " +
                    myPac[pagina - 1].nombre
                  }
                  description={myPac[pagina - 1].observaciones}
                  extra={
                    <div>
                      {myPac[pagina - 1].anexo ? (
                        <Image
                          size="small"
                          src={myPac[pagina - 1].anexo}
                        ></Image>
                      ) : null}
                      <h2>PLAN</h2>
                      <Segment>
                        <Label>Fecha Limite Inicial</Label>
                        <Label>
                          {xltoDate(myPac[pagina - 1].fecha_cierre)}
                        </Label>
                        {myPac[pagina - 1].extension === "Y" ? (
                          <Checkbox
                            disabled
                            label={{ children: "Extender Fecha Limite " }}
                            checked
                          />
                        ) : (
                          <Checkbox
                            label={{ children: "Extender Fecha Limite " }}
                            onChange={() => {
                              if (extension !== "N") {
                                setExtension("N");
                              } else {
                                setExtension("Y");
                              }
                            }}
                            checked={extension === "Y"}
                          />
                        )}
                        {myPac[pagina - 1].extension === "Y" ||
                        extension === "Y" ? (
                          <Input
                            size="mini"
                            type="date"
                            onChange={(e) => {
                              console.log(e.target.value);
                              setExtDate(e.target.value);
                              console.log(typeof e.target.value);
                            }}
                            defaultValue={
                              myPac[pagina - 1].extension === "Y"
                                ? xltoDate(myPac[pagina - 1].nueva_fecha)
                                : date
                            }
                          />
                        ) : null}
                      </Segment>
                      <Segment>
                        <Label fluid>{myPac[pagina - 1].pac}</Label>
                      </Segment>
                      <Segment>
                        <Label>Detalles:</Label>{" "}
                        <span style={{ textColor: "red" }}>(*)</span>
                        <br />
                        <textarea
                          id="pacDetail"
                          name="planDetail"
                          rows="4"
                          cols="60"
                          onChange={(e) => {
                            setDetalle(e.target.value);
                          }}
                        ></textarea>
                        <br />
                        <Label>Anexo:</Label>
                        <br />
                        <form onSubmit={handleFireBaseUpload}>
                          <Input
                            id="anexoImg"
                            fluid
                            className="input-image"
                            type="file"
                            action={{
                              content: "Subir Imagen",
                              icon: "camera",
                            }}
                            onChange={handleImageUpload}
                          />
                          <br />
                        </form>
                        <Image size="large" id="blah" src="#" alt="Anexo" />
                      </Segment>
                    </div>
                  }
                ></Card>
                <div className="ui two buttons">
                  <Button color="yellow" onClick={registrar}>
                    Guardar
                  </Button>
                  <Button color="green">Solicitar Aprobacion</Button>
                </div>
              </>
            ) : null}
          </Segment>
        </>
      ) : (
        <Button
          color="red"
          inverted
          fluid
          icon="key"
          content="Sign In"
          onClick={() => {
            iniciar();
          }}
        />
      )}
    </Container>
  );
}

export default Tareas;
