import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import { storage } from "./firebase/firebase";
import {
  Container,
  Header,
  Statistic,
  Icon,
  Image,
  Segment,
  Button,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import "react-datepicker/dist/react-datepicker.css";
import imageCompression from "browser-image-compression";
import userData from "./employee.json";

let datas = [];

class Tabla {
  constructor(
    fecha,
    fecha_hr,
    nombre,
    area,
    cat,
    rubro,
    obs,
    pac,
    fecha_pac,
    resp,
    src
  ) {
    this.fecha = fecha;
    this.fecha_hr = fecha_hr;
    this.nombre = nombre;
    this.area = area;
    this.cat = cat;
    this.rubro = rubro;
    this.obs = obs;
    this.pac = pac;
    this.fecha_pac = fecha_pac;
    this.resp = resp;
    this.src = src;
  }
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.fecha;
  const fechaB = b.fecha;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

function dateFormat(num) {
  if (num) {
    const fechaxl = new Date((num - (25567 + 1)) * 86400 * 1000);
    let xldd = fechaxl.getDate();
    let xlmm = fechaxl.getMonth() + 1;
    let xlyyyy = fechaxl.getFullYear();
    if (xldd < 10) xldd = "0" + xldd;
    if (xlmm < 10) xlmm = "0" + xlmm;
    let xldate = xlyyyy + "-" + xlmm + "-" + xldd;
    return xldate;
  }
}

function Perfil({ user }) {
  const [conteo, setConteo] = useState(0);
  const [incidentes, setIncidentes] = useState("");
  const [resp, setResp] = useState("");

  const sacardata = async () => {
    datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    let lr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:K" + lr.toString());
    for (j = 1; j < lr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      const cr = sheet.getCell(j, 2);
      const dr = sheet.getCell(j, 3);
      const er = sheet.getCell(j, 4);
      const fr = sheet.getCell(j, 5);
      const gr = sheet.getCell(j, 6);
      const hr = sheet.getCell(j, 7);
      const ir = sheet.getCell(j, 8);
      const jr = sheet.getCell(j, 9);
      const kr = sheet.getCell(j, 10);
      datas.push(
        new Tabla(
          dateFormat(ar.value),
          dateFormat(br.value),
          cr.value,
          dr.value,
          er.value,
          fr.value,
          gr.value,
          hr.value,
          dateFormat(ir.value),
          jr.value,
          kr.value
        )
      );
    }
    console.log(datas);
    setIncidentes(
      datas.filter((item) => {
        if (item.nombre === user) return true;
      })
    );
    setResp(
      datas.filter((item) => {
        if (item.resp === user) return true;
      })
    );
  };

  useEffect(() => {
    if (conteo === 0) {
      if (!incidentes) {
        sacardata();
      }
      setConteo(1);
    }
  }, [conteo, incidentes]);

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      console.log(ele);
      if (ele === "Sign In") {
        priceEls[i].click();
        break;
      }
    }
  };
  return (
    <Container>
      <br />
      {user ? (
        <Segment>
          <Statistic.Group size="mini">
            <Statistic>
              <Statistic.Value>
                <Icon name="pencil alternate" />3
              </Statistic.Value>
              <Statistic.Label>INC. REPORTADOS</Statistic.Label>
            </Statistic>
          </Statistic.Group>
          <Statistic.Group size="mini">
            <Statistic color="yellow">
              <Statistic.Value>
                <Icon name="attention" />5
              </Statistic.Value>
              <Statistic.Label>PAC</Statistic.Label>
            </Statistic>

            <Statistic color="red">
              <Statistic.Value>
                <Icon name="exclamation triangle" />3
              </Statistic.Value>
              <Statistic.Label>PAC Abiertos</Statistic.Label>
            </Statistic>
          </Statistic.Group>
        </Segment>
      ) : (
        <Button
          color="red"
          inverted
          fluid
          icon="key"
          content="Sign In"
          onClick={() => {
            iniciar();
          }}
        />
      )}
    </Container>
  );
}

export default Perfil;
