import React, { useState, useEffect } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import { storage } from "./firebase/firebase";
import {
  Container,
  Header,
  Card,
  Icon,
  Image,
  Segment,
  Button,
  Modal,
  Input,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import "react-datepicker/dist/react-datepicker.css";
import imageCompression from "browser-image-compression";
import userData from "./employee.json";

let datas = [];

class Tabla {
  constructor(titulo, desc, image, likes, created, fecha) {
    this.titulo = titulo;
    this.desc = desc;
    this.image = image;
    this.likes = likes;
    this.created = created;
    this.fecha = fecha;
  }
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.fecha;
  const fechaB = b.fecha;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

function dateFormat() {
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  return date;
}

function News({ user }) {
  const [open, setOpen] = React.useState(false);
  const [conteo, setConteo] = useState(0);
  const [incidentes, setIncidentes] = useState([]);
  const [resp, setResp] = useState("");
  const [titulo, setTitulo] = useState("");
  const [detalle, setDetalle] = useState("");
  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const sacardata = async () => {
    datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    console.log(doc);
    const sheet = doc.sheetsById[2122603919];
    const rows = await sheet.getRows();
    let lr = rows.length + 1;
    var j;
    await sheet.loadCells("A1:K" + lr.toString());
    for (j = 1; j < lr; j++) {
      const ar = sheet.getCell(j, 0);
      const br = sheet.getCell(j, 1);
      const cr = sheet.getCell(j, 2);
      const dr = sheet.getCell(j, 3);
      const er = sheet.getCell(j, 4);
      const fr = sheet.getCell(j, 5);
      datas.push(
        new Tabla(
          ar.value,
          br.value,
          cr.value,
          dr.value,
          er.value,
          //arreglar la fecha
          dateFormat(fr.value)
        )
      );
    }
    console.log(datas);
    setIncidentes(
      datas.filter((item) => {
        if (item.created) return true;
      })
    );
  };

  useEffect(() => {
    if (conteo === 0) {
      if (incidentes.length === 0) {
        sacardata();
      }
      setConteo(1);
    }
  }, [conteo, incidentes]);

  async function handleImageUpload(event) {
    const imageFile = event.target.files[0];
    console.log("originalFile instanceof Blob", imageFile instanceof Blob); // true
    console.log(`originalFile size ${imageFile.size / 1024 / 1024} MB`);

    const options = {
      maxSizeMB: 1,
      maxWidthOrHeight: 1920,
      useWebWorker: true,
    };
    try {
      const compressedFile = await imageCompression(imageFile, options);
      console.log(
        "compressedFile instanceof Blob",
        compressedFile instanceof Blob
      ); // true
      console.log(
        `compressedFile size ${compressedFile.size / 1024 / 1024} MB`
      ); // smaller than maxSizeMB
      setImageAsFile((imageFile) => compressedFile);
    } catch (error) {
      console.log(error);
    }
  }

  const handleFireBaseUpload = (e) => {
    e.preventDefault();
    console.log("start of upload");
    console.log(imageAsFile);
    if (imageAsFile === "") {
      console.error(`not an image, the image file is a ${typeof imageAsFile}`);
    }
    const uploadTask = storage
      .ref(`/images/${imageAsFile.name}`)
      .put(imageAsFile);
    uploadTask.on(
      "state_changed",
      (snapShot) => {
        //takes a snap shot of the process as it is happening
        console.log(snapShot);
      },
      (err) => {
        //catches the errors
        console.log(err);
      },
      () => {
        // gets the functions from storage refences the image storage in firebase by the children
        // gets the download url then sets the image from firebase as the value for the imgUrl key:
        storage
          .ref("images")
          .child(imageAsFile.name)
          .getDownloadURL()
          .then((fireBaseUrl) => {
            setImageAsUrl((prevObject) => ({
              ...prevObject,
              imgUrl: fireBaseUrl,
            }));
          });
      }
    );
  };

  const iniciar = () => {
    var priceEls = document.querySelectorAll(".item");
    for (var i = 0; i < priceEls.length; i++) {
      var ele = priceEls[i].innerText;
      console.log(ele);
      if (ele === "Sign In") {
        priceEls[i].click();
        break;
      }
    }
  };

  return (
    <Container>
      <Modal
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}
        open={open}
        trigger={
          <Button icon labelPosition="left">
            <Icon name="add" />
            Nuevo
          </Button>
        }
      >
        <Modal.Header>Crear noticia</Modal.Header>
        <Modal.Content image>
          <Image size="medium" src={imageAsUrl.imgUrl} wrapped />
          <Modal.Description>
            <Header>
              <Input
                fluid
                placeholder="Titulo"
                value={titulo}
                onChange={(e, { value }) => {
                  setTitulo(value);
                }}
              ></Input>
            </Header>
            <p>
              <Input
                fluid
                placeholder="Detalle"
                value={detalle}
                onChange={(e, { value }) => {
                  setDetalle(value);
                }}
              ></Input>
            </p>
            <p>
              <Input
                disabled={subir}
                fluid
                className="input-image"
                type="file"
                onChange={handleImageUpload}
              />
            </p>
            <p>
              <Button
                content="Subir Imagen"
                labelPosition="right"
                icon="checkmark"
                onClick={(e) => {
                  handleFireBaseUpload(e);
                }}
                positive
              />
            </p>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button color="black" onClick={() => setOpen(false)}>
            Nope
          </Button>
          <Button
            content="Yep, that's me"
            labelPosition="right"
            icon="checkmark"
            onClick={async function registrar() {
              const creds = require("./sasso-app.json"); // the file saved above
              const doc = new GoogleSpreadsheet(
                "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
              );
              await doc.useServiceAccountAuth(creds);
              await doc.loadInfo(); // loads document properties and worksheets
              const sheet = doc.sheetsById[2122603919];
              const newData = await sheet.addRow({
                Titulo: titulo,
                Descripcion: detalle,
                Imagen: imageAsUrl.imgUrl,
                Likes: 0,
                Creador: user,
                Fecha: dateFormat(),
              });
              alert(`Datos Guardados`);
              setOpen(false);
            }}
            positive
          />
        </Modal.Actions>
      </Modal>

      <br />
      {user ? (
        incidentes.map((item, i) => {
          return (
            <Segment>
              <Card>
                <Image src={item.image} wrapped ui={false} />
                <Card.Content>
                  <Card.Header>{item.titulo}</Card.Header>
                  <Card.Meta>
                    <span className="date">{item.fecha}</span>
                  </Card.Meta>
                  <Card.Description>{item.desc}</Card.Description>
                </Card.Content>
                <Card.Content extra>
                  <a>
                    <Icon name="heart" />
                    22 Likes
                  </a>
                </Card.Content>
              </Card>
            </Segment>
          );
        })
      ) : (
        <Button
          color="red"
          inverted
          fluid
          icon="key"
          content="Sign In"
          onClick={() => {
            iniciar();
          }}
        />
      )}
    </Container>
  );
}

export default News;
